
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
export default {
components: {
Group,
XHeader,
Cell,
Tabbar,
TabbarItem,
Icon,
Loading
},
data () {
return {




msg: 'Hello World!',
state:'hello1',
bottomstyle:'55px'
}
},
mounted(){
document.title = this.$route.meta.title;
this.state = window.location.hash.substr(2);




},
computed:{
...mapState(['direction']),
...mapState({
isLoading: state => state.isLoading
}),
directionhead(){
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
},
headTit(){
return this.$route.meta.title;
},
ifShowbar(){
return this.$route.meta.showback;
},
tabberShow(){
return this.$route.meta.showback;
}
},
watch:{
'$route':function(n){
document.title = n.meta.title;
this.headTit = n.meta.title;
this.ifShowbar = n.meta.showback;
this.state = window.location.hash.substr(2);
if(this.tabberShow){
this.bottomstyle = 0;
this.tabberShow = n.meta.showback;
}else{
this.bottomstyle = '55px';
}
}
},
}



